export default {
	data() {
		return {
			hasSendCode: false,
			codeSecond: 60,
			mobile: '',
			code: ''
		}
	},
	methods: {
		clearIcon() {
			let that = this;
			that.mobile = "";
		},
		getCode() {
			const that = this;
			uni.showLoading({
				title: "请稍等..."
			})
			that.$api.sendvcode({
				"mobile": that.mobile,
				"sendType": 2
			}).then(res => {
				uni.hideLoading();
				if (res.success) {
					that.timeCount();
					uni.showToast({
						title: "验证码发送成功",
						icon: "none"
					})
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})

		},
		timeCount() {
			const that = this;
			that.hasSendCode = true;
			var interval = setInterval(() => {
				--that.codeSecond
			}, 1000)
			setTimeout(() => {
				clearInterval(interval)
				that.hasSendCode = false;
				that.codeSecond = 60;
			}, 60000)
		},
		next() {
			let that = this;
			that.$Router.push({
				path:"/pages/my/forgetpwd2",
				query:{
					code:that.code,
					mobile:that.mobile
				}
			})
		}
	}
}
